import React, { useCallback, useState } from 'react';
import PropTypes from 'prop-types';
import './FindMovie.scss';

import { MovieCard } from '../MovieCard';

const debounce = (func, delay) => {
  let timer;

  return (...args) => {
    clearTimeout(timer);
    timer = setTimeout(func, delay, ...args);
  };
};

export const FindMovie = ({ findMovie, addMovie, movies }) => {
  const [searchValue, setSearchValue] = useState('');
  const [newMovie, setNewMovie] = useState({});
  const [appliedQuery, setAppliedQuery] = useState('');

  const applyQuery = useCallback(
    debounce(setAppliedQuery, 700),
    [],
  );

  const findSearchMovie = async() => {
    const searchMovie = await findMovie(appliedQuery);

    setNewMovie(searchMovie);
  };

  const addWithoutDuplicates = (event) => {
    event.preventDefault();
    const isDuplicate = movies
      .some(movie => movie.imdbId === newMovie.imdbId);

    if (isDuplicate) {
      return;
    }

    addMovie(newMovie);
    setAppliedQuery('');
    setNewMovie({});
  };

  console.log(appliedQuery);

  return (
    <>
      <form onSubmit={addWithoutDuplicates} className="find-movie">
        <div className="field">
          <label className="label" htmlFor="movie-title">
            Movie title
          </label>

          <div className="control">
            <input
              type="text"
              id="movie-title"
              placeholder="Enter a title to search"
              className="input is-danger"
              value={searchValue}
              onChange={(event) => {
                setSearchValue(event.target.value);
                applyQuery(event.target.value);
              }}
            />
          </div>

          { !newMovie.imdbId && (
            <p className="help is-danger">
              Can&apos;t find a movie with such a title
            </p>
          )}
        </div>

        <div className="field is-grouped">
          <div className="control">
            <button
              onClick={findSearchMovie}
              type="button"
              className="button is-light"
            >
              Find a movie
            </button>
          </div>

          <div className="control">
            <button
              type="submit"
              className="button is-primary"
            >
              Add to the list
            </button>
          </div>
        </div>
      </form>

      <div className="container">
        <h2 className="title">Preview</h2>
        {newMovie.imdbId && <MovieCard {...newMovie} />}
      </div>
    </>
  );
};

const movieType = {
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  imgUrl: PropTypes.string.isRequired,
  imdbUrl: PropTypes.string,
};

FindMovie.propTypes = {
  findMovie: PropTypes.func.isRequired,
  addMovie: PropTypes.func.isRequired,
  movies: PropTypes.arrayOf(
    PropTypes.shape(movieType).isRequired,
  ).isRequired,
};
